import uniRate from "@/components/uni-rate/uni-rate.vue"
	export default {
		data() {
			return {
				moneyValue: '',
				fzhdx: '',
				qrcodeAdress: '',
				ratedata: '',
				type:'',
				data: {
					industry: 0,
					healthy: 0,
				},
				industryList: [{
						name: "0",
						value: "陕西"
					},
					{
						name: "1",
						value: "广东"
					},
					{
						name: "2",
						value: "台湾"
					},
				],
				percent: 0,
				rateValue: 3,
				changeimg: 'https://img2.woyaogexing.com/2019/12/22/6a78aeadbf7f4a32825f3486c692b811!400x400.jpeg', // 更改后的头像
			};
		},
		components: {uniRate},
		onLoad: function (option) { //option为object类型，会序列化上个页面传递的参数
		    console.log(option.type); //打印出上个页面传递的参数。
			this.type=option.type
		},
		watch: {
			ratedata(e) {
				this.rateValue = e;
				// console.log(this.rateValue)
			}
		},
		methods: {
			pickerChange: function(e, val) {
				console.log('picker发送选择改变，携带值为', e.target.value)
				this.data[val] = e.target.value
			},
			rechargeBtn(){
				uni.showToast({
					title: '充值成功',
					icon: 'success',
					duration: 2000
				})
			},
			cI: function() {
				uni.chooseImage({
					success: (chooseImageRes) => {
						const tempFilePaths = chooseImageRes.tempFilePaths;
						this.changeimg = tempFilePaths[0]
						uni.uploadFile({
							url: app.apiUrl + 'small/index/indexdata', //仅为示例，非真实的接口地址
							filePath: tempFilePaths[0],
							name: 'headimgurl',
							formData: {
								openid: uni.getStorageSync('openid')
							},
							success: (res) => {
								console.log(JSON.parse(res.data))
								var res = JSON.parse(res.data)
								if (res.status) {
									app.getUserData() //这里调用用户信息接口更新数据存进vuex
									uni.showToast({
										title: res.msg,
										icon: 'none',
										duration: 2000
									})
								} else {
									uni.showToast({
										title: res.msg,
										icon: 'none',
										duration: 2000
									})
								}
							}
						});
					}
				});
			},
			rateValuedata(e) {
				console.log(e)
				this.rateValue = e.detail.value
			},
			getdata() {
				uni.showToast({
					title: '发布成功',
					icon: 'success',
					duration: 2000
				})
				setTimeout(function() {
					uni.switchTab({
						url: '/pages/index/index'
					});
				}, 1000)

			}
		}
	};
